		console.log(JSON.stringify(this.newsitems));
		//初始化每个商品的page
		for (var p = 0; p < this.tabBars.length; p++) {
			if (p < 1) {
				let tabPage = [1, 1, 1];
				this.pages.push(tabPage);
			}
			let initPage = 1;
			this.pages.push(initPage);
		}
		//全部频道初始化
		this.getCategory();
		let winH = uni.getSystemInfoSync().windowHeight;
		this.height = winH - winH * 0.2;
	},
