		/* 横向更多商品 */
		moreGoods(type) {
			switch (type) {
				case 'jpbk':
					type = '极品爆款'
					break;
				case 'tmcs':
					type = '天猫超市';
					break;
				case 'tmgj':
					type = '天猫国际';
					break;
			}
			uni.navigateTo({
				url: `/pages/common/moreGoods?type=${type}`
			})
		},
		/* 平台活动 */
		pthd(aid) {
			console.log(aid);
			let relationId = uni.getStorageSync("user").pid;
			let data = {
				aid: aid,
				relationId: relationId
			};
			pthd(data).then(res => {
				console.log(res);
				// this.pthdUrl=res.data.result.url;
				const bcPlugin = uni.requireNativePlugin('dahui-alibaichuan');
				var url = res.data.result.url;
				bcPlugin.BCGetCoupons(url, result => {
					console.log(JSON.stringify(result));
					uni.reLaunch({
						url: '/pages/index/user'
					})
				});
			})
		},
		//横向滑动
		scroll1(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		/* 首页选项卡商品 */
		onClickItem(index) {
			console.log(index);
			if (this.current !== index) {
				this.current = index
			}
			switch (index) {
				case 0:
					ztkTaobaoJX().then(res => {
