					// 	break;
			}
		},
		/* 横向活动 */
		ztkActivify(params) {
			console.log(params);
			switch (params.type) {
				case '极品爆款':
					ztkActivify({
						page: 1,
						type: params.type
					}).then(res => {
						if (res.status == 200) {
							this.goodsJpbk = res.content;
							let data = res.content;
							for (let item in data) {
								insert_goods_1({
									tao_id: data[item].tao_id,
									pict_url: data[item].pict_url,
									title: data[item].title,
