						// console.log(uni.getStorageSync("nextPageTab" + this.current, res));
						this.tabBars[index].addGoodsFlag = false;
						resolve(res);
					});
					return;
				}
				getGoodsList({
					page: this.pages[index],
					type: this.tabBars[index].name
				}).then(res => {
					console.log(res);
					if (res.result.length < 10) {
						this.loadingType = 2;
						return;
					}
					this.tabBars[index].addGoodsFlag = false;
