						aryItem.tbjx = res.data.res;
					});
					ary.push(aryItem);
				} else {
					aryItem.head = this.tabBars[i].childTabs;
					getGoodsList({
						page: this.page,
						type: this.tabBars[i].name
					}).then(res => {
						// console.log(JSON.stringify(res.result));
