		screentap(sortType) {
			console.log(this.tabBars[this.tabIndex].name);
			if (sortType == '') {
				this.isDefault = true;
				this.isTotal_sales = 0;
				this.isTk_rate = 0;
				this.isPrice = 0;
				this._getGoodsList({
					type: this.tabBars[this.tabIndex].name,
					sorts: sortType
				});
			} else if (sortType == 'total_sales') {
				this.isDefault = false;
				this.isTk_rate = 0;
				this.isPrice = 0;
				if (this.isTotal_sales == 0 || this.isTotal_sales == 1) {
					this._getGoodsList({
						sorts: sortType + '_des',
						type: this.tabBars[this.tabIndex].name
					});
					this.isTotal_sales = 2;
				} else if (this.isTotal_sales == 2) {
					this.sorts.sort1 = sortType + '_asc';
					this._getGoodsList({
						sorts: sortType + '_asc',
						type: this.tabBars[this.tabIndex].name
					});
					this.isTotal_sales = 1;
				}
			} else if (sortType == 'tk_rate') {
				this.isDefault = false;
				this.isTotal_sales = 0;
				this.isPrice = 0;
				if (this.isTk_rate == 0 || this.isTk_rate == 1) {
					this._getGoodsList({
						sorts: sortType + '_des',
						type: this.tabBars[this.tabIndex].name
					});
					this.isTk_rate = 2;
				} else {
					this._getGoodsList({
						sorts: sortType + '_asc',
						type: this.tabBars[this.tabIndex].name
					});
					this.isTk_rate = 1;
				}
			} else if (sortType == 'price') {
				this.isDefault = false;
				this.isTotal_sales = 0;
				this.isTk_rate = 0;
				if (this.isPrice == 0 || this.isPrice == 1) {
					this._getGoodsList({
						sorts: sortType + '_des',
						type: this.tabBars[this.tabIndex].name
					});
					this.isPrice = 2;
				} else {
					this._getGoodsList({
						sorts: sortType + '_asc',
						type: this.tabBars[this.tabIndex].name
					});
					this.isPrice = 1;
				}
			}
		},
